import React from 'react';
import Axios from 'axios';
import Card from '../components/card/Card';
import CardTab from '../components/card/CardTab';
import Status from '../components/status/Status';
import Rating from '../components/rating/Rating';
import FirstChart from './FirstChart';
import Message from '../components/message/Message';

import '../styles/_main.scss';
import upImg from '../../assets/images/up-arrow.png';
import downImg from '../../assets/images/down-arrow.png';

class First extends React.Component {

  constructor(props) {
    super(props);
    this.api = 'https://app.digitalagent.kz';
    this.state = {
      data: {
        allcount: {all: 0, up: 0, down: 0},
        allrate: {all: 0, from: 0, bad: ''},
        organization: {
          up: [],
          down: []
        }
      }
    }
    Axios.get(this.api + '/api/sit/common').then(response => {
      let data = this.state.data;
      data.allcount.all = response.data.count;
      let rate = String(response.data.ratingMonth);
      data.allrate.all = rate.slice(0, 3);
      data.allcount.down = response.data.count; 
      data.allrate.from = 4.3;   
      this.setState({ data: data });
    });
    Axios.get(this.api + '/api/sit/agencytypes/:agency_short_name').then(response => {
      let data = this.state.data;
      data.allcount.up = response.data.reviewProcessed ? response.data.reviewProcessed.month : 0;
      data.allcount.down = response.data.reviewFailed ? response.data.reviewFailed.month : 0;
      data.allcount.up = 110;
      this.setState({ data: data });
    });  
    Axios.get(this.api + '/api/sit/agencytypes/month').then(response => {
      let data = this.state.data;
      let sub_data = response.data.agency_types.map((item) => {
        if (item.rating > 0) {
          item.rating = item.rating.toString().slice(0, 3);
        }
        return item;
      });
      data.organization.up = [].concat(response.data.agency_types).sort((a, b) => b.rating - a.rating).map((item, i) => (i < 5) ? item : null);
      data.organization.down = [].concat(response.data.agency_types).sort((a, b) => a.rating - b.rating).map((item, i) => (i < 5) ? item : null);
      this.setState({ data: data });
      console.log(data)
    });            
  }  
  
  
  render() {
    return (
    <>
      <div className="page">
        <div className="row">
          <Card title="Количество жалоб" link="/complaints" tabs={['Сегодня', 'Вчера', '7 дней', '30 дней']}>
            <>
              <div className="First__content">
                <p className="text--large text--white">{this.state.data.allcount.all}</p>
                <p className="text--medium">
                  <span className="text--success">{this.state.data.allcount.up}</span> /{' '}
                  <span className="text--danger">{this.state.data.allcount.down}</span>
                </p>
              </div>
              <div className="row">
                <Status text="Обработанные" type="success" />
                <Status text="Не обработанные" type="danger" />
              </div>
            </>
          </Card>
          <Card title="Общий рейтинг" link="/general">
            <>
              <div className="First__content">
                <p className="text--large text--warning">{this.state.data.allrate.all}</p>
                <img src={downImg} alt="up" className="First__arrow" />
                <p className="text--medium">от {this.state.data.allrate.from}</p>
              </div>
              <p>
                Причина : <span className="text--warning">ЦОН Наурызбайский р-н, г. Алматы</span>
              </p>
            </>
          </Card>
          <div className="">
          <Card title="Лучший город" link="/" >
            <>
              <div className="First__content">
                <p className="text--large text--white" style={{ marginRight: '2rem' }}>
                  4.35
                </p>
                <p className="text--medium">г Алматы</p>
              </div>
              <div>
                Лучшая организация :{' '}
                <span className="text--warning">
                  Поликлиника №3 ( <Rating rating="4.6" ratingSize="small" /> )
                </span>
              </div>
            </>
          </Card>
          </div>
          <div className="">
          <Card title="Рейтинг Министерств" link="/" tabs={['Худшие', 'Лучшие']}>
            <>
              <div className="First__content First__ratings">
                <Rating rating="0.3" title="МИД РК" ratingSize="large" ratingColor="danger" />
                <Rating rating="0.4" title="МВД РК" />
                <Rating rating="1.2" title="МО РК" />
                <Rating rating="1.5" title="МИОР РК" />
                <Rating rating="2.1" title="МСХ РК" />
              </div>
              <p>
                Средний рейтинг : <span className="text--warning">3.75</span>
              </p>
            </>
          </Card>
          </div>
        </div>
        <div className="row">
          <div className="column">
            <div className="">
            <Card title="Настроение граждан" link="/">
              <>
                <div className="First__content">
                  <p className="text--large text--white">
                    51<span className="text--medium">%</span>
                  </p>
                  <img src={upImg} alt="up" className="First__arrow" />
                  <p className="text--medium">от 48.5%</p>
                </div>
                <p>
                  Счастливый регион : <span className="text--warning">Алматинская обл.</span>
                </p>
              </>
            </Card>
            </div>
            <div className="">
            <Card title="Активность граждан" link="/" tabs={['Женщин', 'Мужчин']}>
              <>
                <div className="First__content">
                  <p className="text--large text--white" style={{ marginRight: '2rem' }}>
                    77<span className="text--medium">%</span>
                  </p>
                  <p className="text--medium">4 184 граждан</p>
                </div>
                <p>
                  Общее количество : <span className="text--warning">5 437</span>
                </p>
              </>
            </Card>
            </div>
            <div className="">
            <Card title="Прогноз" type="small">
              <p>
                <span className="text--warning">12.4% </span>Вероятность проявления недовольства
              </p>
            </Card>
            </div>
          </div>
          <FirstChart />
          <div className="column">
            <div className="">
            <Card title="Рейтинг Акиматов" link="/" tabs={['Худшие', 'Лучшие']}>
              <>
                <div className="First__content First__ratings">
                  <Rating
                    rating="4.7"
                    title="Нур-султан"
                    ratingSize="large"
                    ratingColor="success"
                  />
                  <Rating rating="4.5" title="Алматы" />
                  <Rating rating="4.0" title="Шымкент" />
                  <Rating rating="3.9" title="Туркестан" />
                  <Rating rating="3.8" title="Актобе" />
                </div>
                <p>
                  Средний рейтинг : <span className="text--warning">4.0</span>
                </p>
              </>
            </Card>
            </div>
            <div>
              <CardTab title="Рейтинг организаций" link="/" tabs={['Худшие', 'Лучшие']} tabs_up={this.state.data.organization.up} tabs_down={this.state.data.organization.down} />
            </div>
            <div className="">
            <Card title="События" type="small">
              <div>
                10:00{' '}
                <span className="text--white">
                  ЦЗН г. Алматы <span className="text--success">обработал</span> жалобу ({' '}
                  <Rating rating="4.2" ratingSize="small" /> )
                </span>
              </div>
            </Card>
            </div>
          </div>
        </div>
      </div>
      <div className="">
      <Message
        text="Прошу разобраться! 30 сентября поехала в цон медеуского района хотела подать документы на единовременную адресную помощь, но менеджер ответил гру… "
        title="ЦОН Сарыаркинского района г. Нур-Султан"
      />
      </div>
    </>
    );
  }
}

export default First;
