import React from 'react';
import Header from './components/Header'
import HeaderFirst from './pages/components/header/Header'
import Breadcrumbs from './components/Breadcrumbs'
import Footer from './components/Footer'
import ComplaintsPage from './components/Сomplaints/ComplaintsPage'
import RaitingRegionsPage from './components/RaitingRegions/RaitingRegionsPage'
import First from './pages/first/First';
import {
    BrowserRouter as Router,
    Switch,
    Route
} from 'react-router-dom';

import './styles/app.scss';

function App() {
    return (
        <Router>

            <Switch>
                <Route path='/general' exact>
                    <div className="w-full h-auto app">
                        <Header/>
                        <Breadcrumbs/>
                        <RaitingRegionsPage/>
                        <Footer/>
                    </div>
                </Route>
                <Route path='/complaints' exact>
                    <div className="w-full h-auto app">
                        <Header/>
                        <Breadcrumbs/>
                        <ComplaintsPage/>
                        <Footer/>
                    </div>
                </Route>

                <Route path="/" >
                    <link rel="stylesheet" href="main.css"/>
                    <div className="App first">
                        <HeaderFirst />
                    <First/>
                    </div>

                </Route>


            </Switch>


        </Router>

    );
}

export default App;
