import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Rating from '../../components/rating/Rating';

import '../../styles/_main.scss';
import arrowImg from '../../../assets/images/right-arrow.png';

export default function Card({ title, link, tabs, tabs_up, tabs_down, children, type }) {
  const [activeTab, setActiveTab] = useState(0);

  function update (index) {
    setActiveTab(index)
  }

  return (
    <div className={`Card ${type ? `Card--${type}` : ''}`}>
      <div>
        <p className="Card__title">
          {title}
          {link && (
            <Link to={link} className="Card__title__link">
              <img src={arrowImg} alt="link arrow" />
            </Link>
          )}
        </p>
        {tabs && (
          <div className="Card__tabs">
            {tabs.map((tab, index) => (
              <span
                className={`Card__tabs__item ${activeTab === index ? 'text--warning' : ''} `}
                onClick={() => update(index)}
                key={index}
              >
                {tab}
              </span>
            ))}
          </div>
        )}
      </div>
      <div>
        <div className="First__content First__ratings">
          {activeTab == 0 ?
            tabs_down.map((item, i) =>
                item != null ? i < 1 ? <Rating rating={item.rating} title={item.name} ratingSize="large" ratingColor="danger" /> : <Rating rating={item.rating} title={item.name} /> : ''
            ) 
            :
            tabs_up.map((item, i) =>
                item != null ? i < 1 ? <Rating rating={item.rating} title={item.name} ratingSize="large" ratingColor="success" /> : <Rating rating={item.rating} title={item.name} /> : ''
            )
          }
        </div>
        <p>
          Средний рейтинг : <span className="text--warning">0</span>
        </p>
      </div>
    </div>
  );
}
